.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{margin: 0; padding: 0; box-sizing: border-box;}
header{
  background-color: aliceblue;
  padding: 20px 10px;
  font-weight: bold; 
}
.list{
  padding: 20px 10px;
  border-bottom: 1px solid lightgray;
  display: flex;
  flex-wrap: wrap;
}
.listname{
  width: calc(100% - 100px);
  color: deepskyblue;
}
.modal{
  background-color: #eee;
  position: absolute;
  right: 0;
  top: 100px;
  display: flex;
  flex-direction: column;
  width: 50%;
  padding: 20px;
}
.modal2{
  background-color: lightblue;
  position: absolute;
  right: 0;
  top: 300px;
  display: flex;
  flex-direction: column;
  width: 50%;
  padding: 20px;
}